body {
	font-family: $lato;
	@include fs(28.9);
	@include lh(28.9, 36);
	@include ls(4);
	font-style: italic;
	font-weight: 400;
	background: #FFF;
	&.is-no-scroll {
		height: 100%;
		overflow: hidden; } }

.container {
	margin: 0 auto;
	padding: 0 15px;
	box-sizing: border-box;
	width: 100%;
	max-width: 1140px; }
