.slider {
	margin: 0 auto 50px;
	@include r(1000) {
		margin-bottom: 45px; }
	@include r(900) {
		margin-bottom: 40px; }
	@include r(800) {
		margin-bottom: 30px; }
	@include r(600) {
		margin-bottom: 25px; }

	&-arrow {
		position: absolute;
		top: 50%;
		z-index: 5;
		width: 60px;
		height: 60px;
		@include fs(0);
		background-color: transparent;
		@include r(600) {
			top: 40%; }
		// 	width: 85px
		// 	height: 85px
		@include r(500) {
			top: 35%; }
		@include r(400) {
			top: 30%; }
		@include r(350) {
			top: 25%; }
		&::before {
			content: '';
			position: absolute;
			top: 50%;
			box-sizing: border-box;
			width: 25px;
			height: 25px;
			border: 4px solid $text-light;
			transform: translate(-50%, -50%) rotate(45deg);
			// +r(600)
			// 	width: 35px
			// 	height: 35px
 }			// 	border-width: 5px
		&:hover {
			.slider-arrow-svg {
				fill: darken($text-dark, 20); } }
		&_prev {
			left: 0;
			@include r(600) {
				left: -10px; }
			&::before {
				left: 60%;
				border-top: 0;
				border-right: 0; } }
		&_next {
			right: 0;
			@include r(600) {
				right: -10px; }
			&::before {
				left: 40%;
				border-bottom: 0;
				border-left: 0; } }
		&-svg {
			width: 100%;
			height: 100%;
			fill: $text-dark;
			transition: .25s; } }
	&-slide {
		&__title {
			margin: 0 auto 25px;
			text-align: center;
			font-family: $lato;
			@include fs(70);
			@include lh(70, 70);
			font-weight: 800;
			font-style: italic;
			color: $text-dark;
			@include r(1400) {
				@include fs(60); }
			@include r(1300) {
				margin-bottom: 20px;
				@include fs(50); }
			@include r(1200) {
				margin-bottom: 15px;
				@include fs(40); }
			@include r(1100) {
				margin-bottom: 10px; }
			@include r(700) {
				@include fs(36); } }
		&-img {
			margin-bottom: 35px;
			width: 100%;
			@include r(1000) {
				margin-bottom: 30px; }
			@include r(900) {
				margin-bottom: 25px; }
			@include r(800) {
				margin-bottom: 20px; }
			@include r(600) {
				margin-bottom: 15px; }
			&__img {
				width: 100%;
				height: auto; } }
		&-description {
			box-sizing: border-box;
			padding: 0 20px;
			@include r(1000) {
				padding: 0 15px; }
			@include r(768) {
				text-align: center; }
			@include r(600) {
				padding: 0 10px; }
			@include r(500) {
				padding: 0; }
			&__title {
				margin: 0 0 5px;
				font-family: $lato;
				@include fs(22);
				@include lh(22, 26);
				@include ls(4);
				font-weight: 800;
				font-style: italic;
				color: $text-dark;
				@include r(600) {
					@include fs(20); }
				@include r(400) {
					@include fs(18); } }
			&__text {
				font-family: $lato;
				@include fs(18);
				@include lh(18, 26);
				@include ls(4);
				font-weight: 400;
				font-style: normal;
				color: $text-dark;
				@include r(600) {
					@include lh(18, 22); }
				@include r(400) {
					@include fs(16); } } } }

	.slick-list {
		margin: 0 auto;
		width: perc(12, 10);
		@include r(600) {
			width: calc(100% - 20px); } } }

