.index-screen {
	padding: 115px 0 70px;
	@include image('water.jpg');
	@include cover;
	@include r(1400) {
		padding: 100px 0 60px; }
	@include r(1300) {
		padding: 90px 0 55px; }
	@include r(1200) {
		padding: 80px 0 50px; }
	@include r(1100) {
		padding: 70px 0 45px; }
	@include r(1000) {
		padding: 60px 0 40px; }
	@include r(900) {
		padding: 50px 0 35px; }
	@include r(800) {
		padding: 40px 0 30px; } }
