.header {
	position: fixed;
	top: 0;
	left: 0;
	z-index: 50;
	display: flex;
	justify-content: space-between;
	align-items: center;
	box-sizing: border-box;
	padding: 20px 45px;
	width: 100%;
	border-bottom: 3px solid transition;
	transition: .75s;
	@include r(1400) {
		padding: 15px 25px; }
	@include r(1200) {
		padding: 15px; }
	@include r(992) {
		padding: 10px 25px; }
	&_dark {
		padding: 15px 45px;
		background-color: $text-dark;
		border-bottom: 3px solid $text-light;
		.header {
			&-logo {
				max-width: 90px;
				@include r(1600) {
					max-width: 80px; }
				@include r(1400) {
					max-width: 70px; }
				@include r(1200) {
					max-width: 50px; }
				@include r(992) {
					max-width: 60px; } }
			&-nav-list {
				&-dropdown {
					background-color: $text-dark;
					&-li:hover {
						.header-nav-list-dropdown-li__link {
							color: darken($sky, 40); } } }
				&-li:hover {
					.header-nav-list-li {
						&__link {
							color: darken($sky, 40); }
						&__caret {
							border-top-color: darken($sky, 40); } } } } } }
	&-logo {
		position: relative;
		z-index: 20;
		display: flex;
		justify-content: center;
		align-items: center;
		max-width: 121px;
		max-height: 119px;
		transition: .5s;
		@include r(1600) {
			max-width: 90px; }
		@include r(1400) {
			max-width: 70px; }
		@include r(1200) {
			max-width: 60px; }
		&__img {
			max-width: 100%;
			max-height: 100%;
			width: auto;
			height: auto; } }
	&-nav {
		@include r(992) {
			position: fixed;
			top: 0;
			left: 0;
			z-index: 15;
			box-sizing: border-box;
			padding-top: 140px;
			padding-bottom: 50px;
			width: 100%;
			height: 100%;
			background-color: $text-dark;
			overflow-y: auto;
			transform: translateY(-100%);
			transition: .25s;
			&.is-show {
				transform: translateY(0); } }
		@include r(768) {
			padding-top: 100px; }
		&-list {
			display: flex;
			margin: 0;
			padding: 0;
			list-style: none;
			@include r(992) {
				flex-direction: column;
				align-items: center; }
			&-li {
				position: relative;
				@include r(992) {
					margin-bottom: 15px; }
				&__link {
					display: block;
					padding: 22px;
					font-family: $lato;
					@include fs(20);
					@include lh(20, 20);
					@include ls(4);
					text-decoration: none;
					color: $text-light;
					transition: .25s;
					@include r(1600) {
						padding: 16px; }
					@include r(1400) {
						padding: 10px;
						@include fs(18); }
					@include r(1200) {
						@include fs(16); }
					@include r(992) {
						@include fs(26); } }
				&:hover {
					.header-nav-list-li {
						&__link {
							color: $text-dark;
							@include r(992) {
								color: darken($sky, 40); } }
						&__caret {
							border-top-color: $text-dark;
							@include r(992) {
								border-top-color: darken($sky, 40); } } } }
				&__caret {
					display: inline-block;
					vertical-align: middle;
					box-sizing: border-box;
					width: 0;
					height: 0;
					@include fs(0);
					border: {
						top: 10px solid $text-light;
						left: 5px solid transparent;
						right: 5px solid transparent; }
					background-color: transparent; }
				@include rmin(992) {
					&_dropdown {
						&:hover {
							.header-nav-list-dropdown {
								opacity: 1;
								visibility: visible;
								transform: translateY(0); } } } }
				@include r(992) {
					&_dropdown .header-nav-list-li__link {
						text-align: center; } } }
			&-dropdown {
				position: absolute;
				top: 100%;
				left: 0%;
				box-sizing: border-box;
				padding-bottom: 20px;
				width: 200px;
				max-height: 80vh;
				transition: .25s;
				opacity: 0;
				transform: translateY(-100%);
				overflow-y: auto;
				visibility: hidden;
				@include r(992) {
					position: static;
					display: none;
					opacity: 1;
					visibility: visible;
					transform: translateY(0);
					transition: none;
 }					// &.is-show
				&-li {
					&__link {
						display: block;
						padding: 11px 22px;
						font-family: $lato;
						@include fs(18);
						@include lh(18, 18);
						@include ls(4);
						text-decoration: none;
						color: $text-light;
						transition: .25s;
						@include r(992) {
							text-align: center; }
						&:hover {
							color: $text-dark;
							@include r(992) {
								color: darken($sky, 40); } } } } } }
		&__button {
			display: none;
			@include r(992) {
				position: relative;
				z-index: 20;
				display: block;
				width: 68px;
				height: 40px;
				@include fs(0);
				@include lh(0, 0);
				background-color: transparent; }
			@include r(768) {
				width: 60px; } } } }
