.lines {
  position: relative;
  display: inline-block;
  vertical-align: middle;
  box-sizing: border-box;
  width: 100%;
  height: 5px;
  background-color: #ffffff;
  border-radius: 3px;
  transition: .25s;
}

@media only screen and (max-width: 768px) {
  .lines {
    height: 6px;
  }
}

.lines::before, .lines::after {
  content: '';
  position: absolute;
  right: 0px;
  box-sizing: border-box;
  width: 100%;
  height: 5px;
  border-radius: 3px;
  background-color: #ffffff;
  transition: .25s;
}

@media only screen and (max-width: 768px) {
  .lines::before, .lines::after {
    height: 6px;
  }
}

.lines::before {
  top: -10px;
}

@media only screen and (max-width: 768px) {
  .lines::before {
    top: -10px;
  }
}

.lines::after {
  bottom: -10px;
}

@media only screen and (max-width: 768px) {
  .lines::after {
    bottom: -10px;
  }
}

.is-active .lines {
  background-color: transparent;
}

.is-active .lines::before {
  top: 00%;
  transform: rotate(45deg);
}

.is-active .lines::after {
  bottom: 00%;
  transform: rotate(-45deg);
}

