}

.slider-bg-slide-link:hover {
  color: #ffffff;
  text-decoration: none;
}

.slider-bg-slide-link:hover .slider-bg-slide-link__text {
  color: #1b8aa5;
}

.slider-bg-slide-link:hover .slider-bg-slide-link-img .icon {
  fill: #1b8aa5;
}

.slider-bg-slide-link__text {
  display: block;
  margin-bottom: 10px;
  font-family: "lato", sans-serif;
  font-size: 30px;
  line-height: 1;
  font-style: italic;
  color: #ffffff;
