.index-items {
	width: 100%;
	color: $text-light;
	&_marine {
		background-color: $marine;
		.index-item {
			background-color: $marine;
			&:hover {
				background-color: darken($marine, 5);
				&::before {
					opacity: 1; }
				.index-item-cost .icon {
					fill: darken($marine, 5); } } } }
	&_pink {
		background-color: $pink;
		.index-item {
			background-color: $pink;
			&::after {
				background-image: linear-gradient(to bottom, transparent, transparent, $pink); }
			&::before {
				background-image: linear-gradient(to bottom, transparent, transparent, darken($pink, 5)); }
			.index-item-cost .icon {
