				background-color: darken($sky, 5);
				&::before {
					opacity: 1; }
				.index-item-cost .icon {
					fill: darken($sky, 5); } } } }
	&-title {
		position: relative;
		&__title {
			position: relative;
			z-index: 5;
			display: flex;
			justify-content: center;
			align-items: center;
			box-sizing: border-box;
			margin: 0;
			padding: 5px 15px;
			min-height: 155px;
			text-align: center;
			font-family: $lato;
			@include fs(60);
			@include lh(60, 70);
			font-weight: 800;
			font-style: italic;
			@include r(1400) {
				min-height: 125px;
				@include fs(50); }
			@include r(1000) {
				min-height: 100px; }
			@include r(750) {
				min-height: 90px;
				@include fs(40); }
			@include r(500) {
				min-height: 80px;
				@include fs(30); }
			@include r(400) {
				@include fs(28); }
			@include r(320) {
				@include fs(26); }
			.icon {
				margin-right: 15px;
				width: 75px;
				height: 75px;
				fill: $text-light;
				@include r(1400) {
					width: 70px;
					height: 70px; }
				@include r(1000) {
					margin-right: 10px;
					width: 60px;
					height: 60px; }
				@include r(750) {
					width: 50px;
					height: 50px; }
				@include r(500) {
					width: 40px;
					height: 40px; }
				@include r(400) {
					width: 35px;
					height: 35px; } } } }
	&-wrapper {
		display: flex;
		flex-wrap: wrap;
		border-top: 3px solid $text-light;
		&__item {
			box-sizing: border-box;
			width: 25%;
			// padding-bottom: 100%
			min-height: 480px;
			border-bottom: 3px solid $text-light;
			&:not(:last-of-type) {
				border-right: 3px solid $text-light; }
			@include r(1200) {
				min-height: 400px; }
			@include r(1000) {
				min-height: 375px; }
			@include r(900) {
				width: 50%;
				&:not(:last-of-type) {
					border-right: 0; }
				&:nth-of-type(odd) {
					border-right: 3px solid $text-light; } }
			@include r(500) {
				min-height: 300px; }
			@include r(450) {
				width: 100%;
				&:nth-of-type(odd) {
					border-right: 0; } } } } }

.index-item {
	position: relative;
	display: flex;
	flex-direction: column;
	justify-content: flex-end;
	align-items: center;
	box-sizing: border-box;
	padding: 20px;
	text-align: center;
	color: $text-light;
	text-decoration: none;
	overflow: hidden;
	transition: .25s;
	&::after,
	&::before {
		content: '';
		position: absolute;
		bottom: 0;
		left: 0;
		width: 100%;
		height: 100%;
		background-image: linear-gradient(to bottom, transparent, transparent, $marine);
		transition: .25s; }
	&::before {
		z-index: 1;
		background-image: linear-gradient(to bottom, transparent, transparent, darken($marine, 5));
		opacity: 0; }
	&:hover {
		color: $text-light;
		text-decoration: none; }
	&_banner {
		justify-content: center;
		&::before,
		&::after {
			display: none; } }
	&-banner {
		position: absolute;
		top: 20px;
		right: 20px;
		z-index: 2;
		display: flex;
		align-items: center;
		justify-content: center;
		font-family: $lato;
		@include fs(18);
		font-weight: 800;
		font-style: italic;
		color: $text-light;
		&::before,
		&::after {
			content: '';
			display: inline-block;
			width: 22px;
			height: 1px;
			background-color: $text-light; }
		&::before {
			margin-right: 5px; }
		&::after {
			margin-left: 5px; } }
	&__banner {
		max-width: 100%;
		max-height: 100%;
		width: auto;
		height: auto; }
	& > *:not(.index-item__img):not(.index-item-cost):not(.index-item-banner) {
		position: relative;
		z-index: 5; }
	&__img {
		position: absolute;
		top: 50%;
		left: 50%;
		z-index: 0;
		min-width: 100%;
		min-height: 100%;
		width: auto;
		height: auto;
		transform: translate(-50%, -50%); }
	&-cost {
		position: absolute;
		top: 20px;
		right: 20px;
		z-index: 2;
		min-width: 112px;
		min-height: 112px;
		display: flex;
		justify-content: center;
		align-items: center;
		text-align: center;
		@include r(1400) {
			min-width: 105px;
			min-height: 105px; }
		@include r(1200) {
			min-width: 100px;
			min-height: 100px; }
		@include r(1000) {
			min-width: 90px;
			min-height: 90px; }
		.icon {
			position: absolute;
			top: 0;
			right: 0;
			box-sizing: border-box;
			width: 100%;
			height: 100%;
			fill: $marine;
			stroke: $text-light;
			animation: rotate 10s linear infinite;
			animation-play-state: paused;
			transition: .25s; }
		&:hover {
			.icon {
				animation-play-state: running; } }
		&__text {
			position: relative;
			z-index: 5;
			font-family: $lato;
			@include fs(22);
			@include lh(22, 22);
			@include ls(4);
			font-weight: 800;
			font-style: italic;
			color: $text-light;
			@include r(1400) {
				@include fs(20); }
			@include r(1200) {
				@include fs(18); }
			@include r(1000) {
				@include fs(16); } } }
	&__title,
	&__text {
		margin: 0;
		text-align: center;
		font-family: $lato;
		@include fs(22);
		@include lh(22, 30);
		font-weight: 800;
		font-style: italic;
		color: $text-light;
		@include r(1200) {
			@include fs(20); }
		@include r(1000) {
			@include fs(18); } } }


@keyframes rotate {
	100% {
		transform: rotate(360deg); } }
